.container{
    text-align: center;
}
.title{
    color: rgb(255, 255, 255);
    margin-top: 50px;
    font-size: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.title span{
    color: rgb(0, 255, 255);
    padding-left: 15px;
}

.reset{
    height: 100px;
    width: 250px;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 50px;
    background-color: rgb(13, 27, 13);
    font-weight: bold;
    color: aquamarine;
    margin-top: 25px;
    margin-bottom: 50px;
    margin-left: -30px;
    font-size: 20px;
    align-content: center;
    text-align: center;
    align-items: center;
}

.boxes{
    display: flex;
    height: 130px;
    width: 130px;
    background: #1f3540;
    border: 4px solid #0f1b21;
    border-radius: 12px;
    cursor: pointer;
}

.board{
    height: 450px;
    width: 464px;
    display: flex;
    margin: auto;
    align-items: center;
    align-content: center;

}

.boxes img{
    margin: 30px 30px;
}

.title img{
    padding: 20px 20px;
    height: 40px;
    margin-top: 10px;
}

@media only screen and (max-width: 600px) {
    .container{
        text-align: center;
    }
    .title{
        color: rgb(255, 255, 255);
        margin-top: 60px;
        font-size: 25px;
        text-align: center;
        display: flex;
        justify-content: center;
        margin-left: 15%;
        
    }
    
    .title span{
        color: rgb(0, 255, 255);
        padding-left: 15px;
        text-align: center;
    }
    
    .reset{
        height: 70px;
        width: 150px;
        border: none;
        outline: none;
        cursor: pointer;
        border-radius: 50px;
        background-color: rgb(13, 27, 13);
        font-weight: bold;
        color: aquamarine;
        margin-top: 70px;
        margin-bottom: 50px;
        margin-left: 40%;
        font-size: 20px;
        display: flex;
        justify-content: center;
        align-content: center;
        text-align: center;
    }
    
    .boxes{
        display: flex;
        height: 120px;
        width: 120px;
        background: #1f3540;
        border: 4px solid #0f1b21;
        border-radius: 12px;
        cursor: pointer;
        display: flex;
        justify-content: space-between;
    }
    
    .board{
        height: 450px;
        width: 464px;
        display: flex;
        margin-top: 50px;
        display: flex;
        justify-content: center;
    
    }
    
    .boxes img{
        margin: 30px 30px;
    }
    
   
  }